import { mapState } from 'vuex';
import event from '@/common/utils/event.js';

import {
    fetchAuditList,
    fetchAddArtProject,
    fetchbindArtAddress,
    fetchProjectActive,
    fetchProjectList,
    fetchVipList,
    fetchEnableProjectToVip,
    fetchBindProjectToVip,
} from '../../../contactLogic/manage/index';

export default {
    name: 'artList',
    data() {
        return {
            activeItem: 'one',
            nextProjectID: -1,
            manualProjectId: '',
            manualArtistAddress: '',
            auditList: [],
            projectList: [],
            vipProjects: [],
            loading: false,
        };
    },
    components: {
      SetCardDialog: () => import("./SetCardDialog.vue"),
    },
    computed: {
        ...mapState(['web3', 'ethersprovider', 'ethChainID', 'ethAddress']),
        isReady() {
            return this.web3 && this.ethersprovider && this.ethChainID && this.ethAddress;
        },
        productsCols() {
            return [{
                    title: 'Name',
                    slot: "projectName",
                    minWidth: 25,
                },
                {
                    title: 'Artist Address',
                    slot: "projectArtistAddress",
                    minWidth: 100,
                },
                {
                    title: 'Artist',
                    slot: "projectArtist",
                    minWidth: 30,
                },
                {
                    title: 'Mint Price',
                    slot: "projectMintPrice",
                    minWidth: 40,
                },
                {
                    title: 'Type',
                    slot: "iscode",
                    minWidth: 80,
                },
                {
                    title: 'Description',
                    slot: "projectdescription",
                    minWidth: 60,
                },
                {
                    title: 'License',
                    slot: "projectLicense",
                    minWidth: 40,
                },
                {
                    title: 'Website',
                    slot: "projectWebsite",
                    minWidth: 50,
                },
                {
                    title: '添加项目',
                    slot: "addProject",
                    minWidth: 20,
                },
                {
                    title: '绑定地址',
                    slot: "addAddress",
                    minWidth: 20,
                },
            ];
        },
        activeCols() {
            return [{
                    title: 'ID',
                    slot: "project_id",
                },
                {
                    title: 'Name',
                    slot: "projectName",
                    minWidth: 100,
                },
                {
                    title: 'Artist',
                    slot: "artist",
                },
                {
                    title: 'Description',
                    slot: "description",
                    minWidth: 120
                },
                {
                    title: 'License',
                    slot: "license",
                },
                {
                    title: 'Website',
                    slot: "website",
                    minWidth: 120
                },
                {
                    title: 'Active',
                    slot: "active",
                    minWidth: 20,
                },
                {
                    title: 'Action',
                    slot: "action",
                    minWidth: 20,
                },
                {
                    title: '设置会员卡',
                    slot: "bind",
                    width: 160,
                },
            ];
        },
    },
    methods: {
        async loadData() {
            try {
                this.loading = true;
                const params = this.getParams();
                this.auditList = await fetchAuditList();
                this.vipProjects = await fetchVipList();
                this.projectList = await fetchProjectList({
                  ...params,
                  all: true,
                });
            } catch (error) {
                console.log(error);
            } finally {
                this.loading = false;
            }
        },
        getParams() {
            return {
                web3: this.web3,
                library: this.ethersprovider,
                account: this.ethAddress,
                chainID: this.ethChainID
            };
        },
        async onAddClick(row) {
            const params = this.getParams();
            const { ProjectID } = await fetchAddArtProject({
                ...row,
                ...params
            });
            console.log({ ProjectID });
            this.nextProjectID = ProjectID;
        },
        async onBindAddress(row) {
            if (this.nextProjectID === -1) return;
            const params = this.getParams();
            const res = await fetchbindArtAddress({
                ...row,
                ...params,
                nextProjectID: this.nextProjectID,
            });
            event.$emit('sendtx', [
                res,
                {
                    okinfo: `Bind Success!`,
                    failinfo: `Bind Failed！`,
                },
            ]);
        },
        async onActiveClick(row) {
            const params = this.getParams();
            const res = await fetchProjectActive({
                ...params,
                projectId: row.project_id,
            });
            event.$emit('sendtx', [
                res,
                {
                    okinfo: `Active Success!`,
                    failinfo: `Active Failed！`,
                },
            ]);
        },
        async onManualBindAddress() {
            const params = this.getParams();
            const res = await fetchbindArtAddress({
                ...params,
                nextProjectID: this.manualProjectId,
                projectArtistAddress: this.manualArtistAddress,
            });
            event.$emit('sendtx', [
                res,
                {
                    okinfo: `Bind Success!`,
                    failinfo: `Bind Failed！`,
                },
            ]);
        },
        async onSetEnableClick(row) {
          const params = this.getParams();
          const res = await fetchEnableProjectToVip({
            ...params,
            projectId: row.project_id.toString(),
          });
          event.$emit('sendtx', [
            res,
            {
              okinfo: `启用 Success!`,
              failinfo: `启用 Failed！`,
            },
          ]);
        },
        async onBindProjectToVip({projectId, passportProjectId}) {
            const params = this.getParams();
            const res = await fetchBindProjectToVip({
              ...params,
              projectId: projectId.toString(),
              passportProjectId: passportProjectId.toString(),
            });
            event.$emit('sendtx', [
              res,
              {
                okinfo: `分配 Success!`,
                failinfo: `分配 Failed！`,
              },
            ]);
            return res;
        },
        onSelect(name) {
            this.activeItem = name;
        },
        onSetCardClick(row) {
            this.$refs.setCard.open(row, this.vipProjects);
        },
        onAllProjectsClick() {
          this.$router.push('/manage/allProjects');
        }
    },
    watch: {
        isReady(val) {
            if (val) this.loadData();
        }
    },
    created() {
        if (this.isReady) {
            this.loadData();
        }
    }
};
